import React from "react";

function InputGroup({ label, onChange, value, errorMsg, inputType }) {
  return (
    <React.Fragment>
      <div className="mb-3 oneline">
        <label htmlFor="lastName" className="form-label">
          {label}
        </label>
        <input
          type={inputType}
          className="form-control"
          onChange={onChange}
          value={value}
        />
      </div>
      <div className={"form-text " + (errorMsg ? "error" : "hidden")}>
        {errorMsg}
      </div>
    </React.Fragment>
  );
}

InputGroup.defaultProps = {
  inputType: "text",
};

export default InputGroup;
